@import '../../../assets/styles/app';

.item {
  position: relative;
  background: rgba(21, 24, 40, 0.8);
  border-radius: 15px;
  border: 1px solid rgba(255, 255, 255, 0.06);
  padding: 20px 20px 24px;

  @include desktop {

  }

  &:not(:first-child) {
    margin-top: 20px;
  }

  .btn {
    @include button;
    position: absolute;
    top: 30px;
    right: 20px;
    width: 25px;
    height: 25px;

    svg {
      width: 25px;
      height: 25px;

      @include desktop {
        width: 30px;
        height: 30px;
      }
    }

    @include desktop {
      top: 15px;
      right: 20px;
      width: 30px;
      height: 30px;
    }
  }

  .question {
    font-weight: 600;
    font-size: 16px;
    line-height: 130%;
    letter-spacing: -0.02em;
    color: #FFFFFF;
    width: 188px;

    @include desktop {
      font-size: 18px;
      width: auto;
    }

  }

  .answer {
    margin-top: 20px;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    letter-spacing: -0.01em;
    color: #A1A3A9;

    @include desktop {
      width: 610px;
    }
  }



}
